@keyframes blink {
  from, to {
    border-color: #0000;
  }

  50% {
    border-color: #05ff00;
  }
}

@keyframes main_background {
  0% {
    -webkit-backdrop-filter: hue-rotate();
    backdrop-filter: hue-rotate();
    background-position: 0 75%;
  }

  50% {
    background-position: 75% 150%;
  }

  100% {
    -webkit-backdrop-filter: hue-rotate(360deg);
    backdrop-filter: hue-rotate(360deg);
    background-position: 0 75%;
  }
}

@keyframes main_ani_title {
  0% {
    text-shadow: 0 0 7px #fff, 0 0 10px #fff, 0 0 16px #fff;
  }

  100% {
    text-shadow: 0 0 #fff, 0 0 #fff, 0 0 #fff;
  }
}

@keyframes minecraft-shake {
  0%, 100% {
    transform: translateX(0)perspective(1000px)rotateX(15deg);
  }

  10%, 30%, 50%, 70% {
    transform: translateX(-10px)perspective(1000px)rotateX(15deg);
  }

  20%, 40%, 60% {
    transform: translateX(10px)perspective(1000px)rotateX(15deg);
  }

  80% {
    transform: translateX(8px)perspective(1000px)rotateX(15deg);
  }

  90% {
    transform: translateX(-8px)perspective(1000px)rotateX(15deg);
  }
}

@keyframes shake-vertical {
  0%, 100% {
    transform: translateY(0);
  }

  10%, 30%, 50%, 70% {
    transform: translateY(-8px);
  }

  20%, 40%, 60% {
    transform: translateY(8px);
  }

  80% {
    transform: translateY(6.4px);
  }

  90% {
    transform: translateY(-6.4px);
  }
}

@keyframes heartbeat {
  from {
    transform-origin: center;
    animation-timing-function: ease-out;
    transform: scale(1);
  }

  10% {
    animation-timing-function: ease-in;
    transform: scale(.91);
  }

  17% {
    animation-timing-function: ease-out;
    transform: scale(.98);
  }

  33% {
    animation-timing-function: ease-in;
    transform: scale(.87);
  }

  45% {
    animation-timing-function: ease-out;
    transform: scale(1);
  }
}

@keyframes stars {
  from {
    transform: translateX(-1000px)translateY(0)scale(1.75);
  }

  to {
    transform: translateX(-1000px)translateY(-2000px)scale(1.75);
  }
}

@keyframes coin {
  from {
    opacity: 0;
    transform: translateY(0);
  }

  to {
    opacity: 1;
    transform: translateY(-180px);
  }
}

@keyframes note {
  0% {
    opacity: 0;
    transform: translateY(0);
  }

  0% {
    opacity: 0;
    transform: translateY(-120px);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 1;
    transform: translateY(-220px);
  }
}

@keyframes laser {
  0% {
    fill-opacity: 0;
    opacity: 0;
    transform: translateY(0);
  }

  12.5% {
    fill-opacity: 1;
    opacity: 1;
    transform: translateY(-40px);
  }

  87.5% {
    fill-opacity: 1;
    opacity: 1;
  }

  100% {
    fill-opacity: 0;
    opacity: 0;
    transform: translateY(-1100px);
  }
}

@keyframes laser-short {
  0% {
    fill-opacity: 0;
    opacity: 0;
    transform: translateY(0);
  }

  12.5% {
    fill-opacity: 1;
    opacity: 1;
    transform: translateY(-40px);
  }

  87.5% {
    fill-opacity: 1;
    opacity: 1;
  }

  100% {
    fill-opacity: 0;
    opacity: 0;
    transform: translateY(-1100px);
  }
}

@keyframes laser-long {
  0% {
    fill-opacity: 0;
    opacity: 0;
    transform: translateY(0);
  }

  12.5% {
    fill-opacity: 1;
    opacity: 1;
  }

  87.5% {
    fill-opacity: 1;
    opacity: 1;
    transform: translateY(-10000px);
  }

  100% {
    fill-opacity: 0;
    opacity: 0;
    transform: translateY(-1000000px);
  }
}

html {
  scroll-behavior: smooth;
}

.scroll-content {
  width: 100%;
}

